$bg-color: #241A33;
$highlight-color: #402f5b;
$wall-color: #666666;
$text-color: #888888;
$rock-color: #884444;
$link-color: #5cc5ed;
$enter-exit-color: $highlight-color;
/* cat suggests E2E2BC for the egg */

body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $bg-color;
  padding: 0px calc(10vmin);
  color: $text-color;
}

ul {
  text-align: left;
}

kbd {
    border-radius: 8px;
    padding: 3px 6px;
    background-color: $text-color;
    color: $bg-color;
    border: 1px solid black;
    font-size: 0.6em;
    bottom: 3px;
    position: relative;
}

code {
    /* actually used for acknowledgements */
    border-radius: 8px;
    padding: 3px 6px;
    background-color: $text-color;
    color: $bg-color;
    font-size: 0.6em;
    bottom: 3px;
    position: relative;
}
.playtesters code {
  font-size: 0.4em;
}
code:has(a) {
  border: 1px solid $link-color;
}
code a {
 text-decoration: none;
}

.menubutton {
  z-index: 1;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 1em;
  border: 1px solid $text-color;
  background-color: $highlight-color;
  cursor: pointer;
}

small {
  font-size: 0.7em;
}

.levelname {
    margin: 20px;
    padding: 20px;
    border-radius: 1em;
    background-color: $highlight-color;
}

.event {
  margin: 10px 0px;
  padding: 0.5em 1em;
  border-radius: 1em;
  border: 0.1em solid $highlight-color;
}
.event.warning {
  border: 0.1em solid red;
}

.controls table {
  margin: auto;
}
.controls table td {
  padding: 0px 10px;
}
.controls table td:nth-child(2) {
  font-size: 0.8em;
  text-align: right;
}
.controls table td:nth-child(3) {
  font-size: 0.8em;
  text-align: left;
}

.centerdiv {
  margin: auto;
}

.main {
  text-align: center;
  min-height: calc(100vh - 30vmin);

  /* display: flex; */
  /* flex-direction: column; */
  margin-top: 1em;

  align-items: center;
  justify-content: center;
  font-size: calc(2vmin);
}
.main > h3 {
  font-size: calc(3vmin);
}
.main > h2 {
  font-size: calc(4vmin);
}

/* less than 3 */
.huge-cells .gridrow {
  height: calc(10vmin);
}
.huge-cells .cell {
  height: calc(10vmin);
  width: calc(10vmin);
  line-height: calc(10vmin);
}

/* less than 7 */
.big-cells .gridrow {
  height: calc(4vmin);
}
.big-cells .cell {
  height: calc(4vmin);
  width: calc(4vmin);
  line-height: calc(5vmin);
}

/* less than 11 */
.medium-cells .gridrow {
  height: calc(3vmin);
}
.medium-cells .cell {
  height: calc(3vmin);
  width: calc(3vmin);
  line-height: calc(3vmin);
}

.gridrow {
  margin: 0px;
  height: calc(2vmin);
}

.cell {
  height: calc(2vmin);
  width: calc(2vmin);
  line-height: calc(2vmin);
  display: inline-block;
  position: relative;
  /* background-color: $text-color; */
  background-color: $bg-color;
  /* background-color: #a19292; */
}
.cell > div {
  position: absolute;
  width: 100%;
  height: 100%;
}

.player {
  background-size: 100%;
  height: 100%;
  width: 100%;
  margin: 0%;
}

.wall {
  /* background-color: #866bb0; */
  /* background-color: #444444; */
  background-color: $wall-color;
  width: 100%;
  height: 100%;
}
.rock {
  background-color: $rock-color;
  border-radius: 50%;
  width: 68%;
  height: 68%;
  margin: 16%;
}
.exit {
  /* background-color: #77aabb; */
  /* background-color: $bg-color; */
  background-color: $enter-exit-color;
}
.wormholeIn, .exit {
    text-align: center;
    font-size: 0.8em;
    box-sizing: border-box;
    border-radius: 50%;
    /* border-style: dotted; */
    /* border-width: 0.1em; */
    border-color: $text-color;
    border-style: solid;
    border-width: 0.1em;
    width: 84%;
    height: 84%;
    margin: 8%;
}
.wormholeIn.pushable {
  border-style: solid;
}
.big-cells .cell > div > .text {
  /* font-size: calc(1.5vmin) */
}
.event .wormholeIn {
  width: 1em;
  height: 1em;
  margin: 0;
  /* idk what's going on with this hacky stuff, somebody help */
  top: -1em;
  position: relative;
}
.event .wormholeIn .text {
  font-size: 0em;
}
.wormholeIn .text {
    color: $bg-color;
    display: block;
    position: relative;
    font-size: 1em;
    bottom: calc(50% - 0.5em);
    width: 100%;
    margin: auto;
    height: 100%;
}
.wormholeOut, .enter {
    text-align: center;
    font-size: 0.8em;
    border-radius: 50%;
    border-style: dotted;
    border-width: 0.1em;
    box-sizing: border-box;
    background-color: transparent;
    width: 84%;
    height: 84%;
    margin: 8%;
}
.wormholeOut.pushable {
    border-style: solid;
}
.enter {
  color: rgba(0, 0, 0, 0) /* transparent
  /* color: $enter-exit-color; */
}

.item {
  background-size: 100% 100%;
  height: 100%;
  width: 100%;
}

